@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200&display=swap);
.pen-icon {
  color: #b9c3cc;
  cursor: pointer;
}

.card {
  max-width: 540px;
}

.row {
  height: 100%;
}

.select {
    max-width: 75%;
}
.home-link {
  font-size: larger;
  color: white;
}

.link {
  color: rgb(211, 211, 211);
}

.link:hover,
.home-link:hover {
  color: white;
  text-decoration: none;
}

.footer {
  background-color: #157381;
  padding: 0 10%;
  height: 100px;
}

.copyright {
  padding: 10px;
  font-size: small;
  border-top: 1px solid #45b8ca;
  text-align: center;
  color: #3e99a7;
  margin-bottom: 0;
}

.icons-div {
  padding: 10px;
  text-align: center;
}

.icon {
  margin: 10px;
  color: #45b8ca;
}

body {
  min-height: 100vh;
  background: linear-gradient(to top, #bdbdbd, #ffffff);
  font-family: 'Libre Franklin', sans-serif;
}

.siteBody {
  min-height: calc(100vh - 160.8px);
}

img {
  height: 100%;
  object-fit: cover;
}

